.form {
  width: 400px;
  height: 605px;
  background: #ffffff 0% 0% no-repeat padding-box;
  border-radius: 8px;
  opacity: 1;
  display: flex;
  flex-flow: column nowrap;
  align-items: center;
}

.form_title {
  width: 271px;
  height: 34px;
  font: normal normal bold 28px/34px Roboto;
  font-family: Roboto;
  letter-spacing: 0px;
  color: #222222;
  opacity: 1;
  display: flex;
  justify-content: center;
  margin-top: 32px;
  margin-bottom: 37px;
}

.form_sex {
  display: flex;
  flex-flow: row nowrap;
  position: relative;
}
