.form_checkbox {
  height: 17px;
  margin-top: 24px;
  position: relative;
}

.form_checkbox > label {
  height: 100%;

  font: normal normal normal 14px/17px Roboto;
  letter-spacing: 0px;
  color: #222222;
  display: flex;
  align-items: center;
}
.form_checkbox > label > input {
  margin-right: 5px;
  width: 14px;
  height: 14px;
  background: #0094ff 0% 0% no-repeat padding-box;
}

a {
  text-decoration: none;
}
