.submit_button {
  width: 343px;
  height: 62px;
  margin-top: 37px;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
}
.form_button {
  height: 100%;
  width: 100%;
  background: #0094ff 0% 0% no-repeat padding-box;
  border-radius: 31px;
  border: none;
  cursor: pointer;
  outline: none;
}
.inactive {
  background-color: #a2a2a2;
  pointer-events: none;
}

.submit_text {
  position: absolute;
  font: normal normal normal 18px/22px Roboto;
  letter-spacing: 0px;
  color: #ffffff;
  cursor: pointer;
  pointer-events: none;
}
