.form_sex_male {
  font: normal normal normal 14px/17px Roboto;
  letter-spacing: 0px;
  color: #222222;
}
.form_sex_male > label,
.form_sex_female > label {
  display: flex;
  justify-content: center;
  align-items: center;
}

.form_sex_female {
  font: normal normal normal 14px/17px Roboto;
  letter-spacing: 0px;
  color: #222222;
  margin-left: 25px;
}

.form_sex_male > label > input,
.form_sex_female > label > input {
  margin-bottom: 4px;
  width: 14px;
  height: 14px;
  margin-right: 5px;
}
