body {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
#root {
  width: 100vw;
  height: 100vh;
  background-color: #102250;
  display: flex;
  justify-content: center;
  align-items: center;
}

@font-face {
  font-family: "Roboto";
  src: local("Roboto"), url(../fonts/Roboto-Regular.ttf) format("truetype");
}
