.select_container {
  width: 343px;
  height: 50px;
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 29px;
  background: #f5f8fa 0% 0% no-repeat padding-box;
  border-radius: 8px;
  position: relative;
}

.select {
  height: 100%;
  width: 80%;
  background: #f5f8fa 0% 0% no-repeat padding-box;
  border-radius: 8px;
  opacity: 1;
  border: none;
  text-align: left;
  font: normal normal normal 14px/17px Roboto;
  letter-spacing: 0px;
  color: #222222;
  opacity: 1;
  padding-left: 18px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  outline: none;
  cursor: pointer;
}

.select_list {
  position: absolute;
  top: 52px;
  width: 343px;
  height: 196px;
  background: #ffffff 0% 0% no-repeat padding-box;
  box-shadow: 0px 3px 8px #00000026;
  border-radius: 8px;
  opacity: 1;
  z-index: 999;
}

.select_option {
  width: 343px;
  height: 36px;
  text-align: left;
  font: normal normal normal 14px/32px Roboto;
  letter-spacing: 0px;
  color: #222222;
  opacity: 1;
  padding-left: 19px;
}
.select_option:hover {
  background-color: #f5f8fa;
  cursor: pointer;
}

.icon_closed {
  position: absolute;
  right: 20px;
  width: 11px;
  height: 11px;
  background: transparent url("../img/Layer\ 2.svg") 0% 0% no-repeat padding-box;
  opacity: 1;
  cursor: pointer;
  outline: none;
}

.icon_opened {
  position: absolute;
  right: 20px;
  width: 11px;
  height: 11px;
  background: transparent url("../img/Layer\ 2.svg") 0% 0% no-repeat padding-box;
  transform: matrix(-1, 0, 0, -1, 0, 0);
  opacity: 1;
  cursor: pointer;
  outline: none;
}
