.input_container {
  width: 343px;
  height: 50px;
  display: flex;
  flex-flow: row nowrap;
  justify-content: flex-start;
  align-items: center;
  margin-bottom: 21px;
  background: #f5f8fa 0% 0% no-repeat padding-box;
  border-radius: 8px;
  position: relative;
}
.icon.email {
  width: 20px;
  height: 16px;
  background: transparent url("../img/Group\ 845.svg") 0% 0% no-repeat
    padding-box;
  opacity: 1;
  margin-left: 18px;
}
.icon.password {
  width: 16px;
  height: 20px;
  background: transparent url("../img/Group\ 846.svg") 0% 0% no-repeat
    padding-box;
  opacity: 1;
  margin-left: 18px;
}
.input.email,
.input.password,
.input.name {
  height: 100%;
  width: 300px;
  background: #f5f8fa 0% 0% no-repeat padding-box;
  border-radius: 8px;
  opacity: 1;
  border: none;
  text-align: left;
  font: normal normal normal 14px/17px Roboto;
  letter-spacing: 0px;
  color: #222222;
  opacity: 1;
  padding-left: 14px;
  outline: none;
}
.input.name {
  padding-left: 18px;
}
